@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Montserrat&display=swap");

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: #383838;
}

.App {
  height: 100vh;
}
header {
  display: flex;
  justify-content: center;
  background: #303030;
  align-items: center;
  position: fixed;
  top: 0;
  width: 100%;
  font-family: "Bangers", cursive;
}

.title {
  color: #ffffff;
  padding: 1rem;
  text-shadow: 0px 0px 5px #679f4a;
}
.prev,
.next {
  border: none;
  font-weight: bold;
  padding: 0.5rem;
  border-radius: 5px;
  height: 30px;
  cursor: pointer;
  background: transparent;
  color: #fff;
}
.card {
  max-width: 600px;
  min-width: 600px;
  height: 300px;
  background: #303030;
  margin-bottom: 50px;
  display: flex;
  box-shadow: 0px 0px 5px #6fa350;
  align-items: center;
  border-radius: 10px;
}

.card:hover {
  cursor: pointer;
}

.wrapper {
  width: 85%;
  margin-top: 100px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.profile-wrap {
  padding: 1rem;
  text-align: center;
}
.profile {
  border-radius: 20px;
  width: 150px;
}
.profile-big {
  width: 250px;
  cursor: pointer;
  transition: all 0.3s ease;
  border-radius: 10px;
}

.profile-big:hover {
  box-shadow: 0px 0px 5px #8eff4d;
}
.info {
  width: 80%;
}

.active-info {
  width: 80%;
  display: block;
}
.name {
  padding: 0.5rem;
  color: #fff;
  text-align: center;
  letter-spacing: 3px;
  font-family: "Bangers", cursive;
}
h4 {
  color: #8e8e8e;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #6fa350;
  padding: 0.5rem;
  letter-spacing: 2px;
  font-family: "Montserrat", sans-serif;
  font-size: 10px;
}

span {
  padding-block: 0.25rem;
  color: #8eff4d;
}
